const __edenInjectedDataDebugOnce = new Set();
const __edenInjectedDataWarnOnce = new Set();
// /
//  `\\b`JSON  `\\b`  backspace >
const HIDDEN_BLOCK_TAGS = ['imgthink', 'drawprompt', 'imageprompt', 'genimage'];
function hasTag(raw, tagName) {
    return new RegExp('<' + tagName + '(?:\\s[^>]*)?>', 'i').test(raw);
}
function stripHiddenBlocks(raw) {
    let cleaned = raw;
    for (const tag of HIDDEN_BLOCK_TAGS) {
        //  <tag ...>...</tag>
        cleaned = cleaned.replace(new RegExp('<' + tag + '(?:\\s[^>]*)?>[\\s\\S]*?<\\/' + tag + '>', 'gi'), '');
    }
    return cleaned;
}
function parseInjectedText(raw) {
    const cleaned = stripHiddenBlocks(raw);
    //  <content>...</content> / <game>...</game> </option> / 
    const contentMatch = cleaned.match(/(<(?:content|game)(?:\s[^>]*)?>(?![\s\S]*?<(?:content|game)(?:\s[^>]*)?>)[\s\S]*?(?:<\/(?:content|game)>|<\/option>|$))/i);
    const optionMatch = cleaned.match(/(<option(?:\s[^>]*)?>(?![\s\S]*?<option(?:\s[^>]*)?>)[\s\S]*?(?:<\/option>|$))/i);
    const content = contentMatch
        ? contentMatch[1]
            .replace(/^<(?:content|game)(?:\s[^>]*)?>/i, '')
            .replace(/<\/(?:content|game)>\s*$/i, '')
            .replace(/<\/option>\s*$/i, '')
            .trim()
        : '';
    const optionsRaw = optionMatch
        ? optionMatch[1]
            .replace(/^<option(?:\s[^>]*)?>/i, '')
            .replace(/<\/option>\s*$/i, '')
            .trim()
        : '';
    const options = optionsRaw
        ? optionsRaw
            .split(/\r?\n/)
            .map((line) => line.trim())
            .filter(Boolean)
        : [];
    return { content, options };
}
function fetchFromCurrentMessage(isDebug) {
    try {
        //  iframe 
        const messageId = getCurrentMessageId();
        const msg = getChatMessages(messageId)[0];
        const raw = msg?.message ?? msg?.data?.extra_text ?? msg?.text ?? '';
        if (!raw.trim())
            return null;
        const parsed = parseInjectedText(raw);
        const rawHasContent = hasTag(raw, 'content') || hasTag(raw, 'game');
        const rawHasOption = hasTag(raw, 'option');
        if (isDebug && !__edenInjectedDataDebugOnce.has(messageId)) {
            __edenInjectedDataDebugOnce.add(messageId);
            console.debug('[][InjectedData] ', {
                messageId,
                rawLen: raw.length,
                rawHasContent,
                rawHasOption,
                contentLen: parsed.content.length,
                optionsCount: parsed.options.length,
                optionsPreview: parsed.options.slice(0, 3),
            });
        }
        //  <option>  <content> ()
        if (!rawHasContent && rawHasOption && !__edenInjectedDataWarnOnce.has(messageId)) {
            __edenInjectedDataWarnOnce.add(messageId);
            console.warn('[][InjectedData]  <content>/<game> <option>', {
                messageId,
                optionsCount: parsed.options.length,
            });
        }
        if (!parsed.content && parsed.options.length === 0)
            return null;
        return parsed;
    }
    catch (e) {
        console.error('[InjectedData] :', e);
        return null;
    }
}
function getMockData() {
    return {
        content: `

<p><p>
  <strong></strong>
  
</p>
<p>
  <em>""""</em> 
</p>
  
</p>
`,
        options: ['', '', ''],
    };
}
export function useInjectedData() {
    const content = ref('');
    const options = ref([]);
    //  ( URL )
    const search = new URLSearchParams(window.location.search);
    const isDevMode = search.has('dev');
    const isDebug = isDevMode || search.has('debug');
    const refresh = () => {
        if (isDevMode) {
            const mockData = getMockData();
            content.value = mockData.content;
            options.value = mockData.options;
            return;
        }
        const fromMsg = fetchFromCurrentMessage(isDebug); // 
        if (fromMsg) {
            content.value = fromMsg.content;
            options.value = fromMsg.options;
            return;
        }
    };
    onMounted(() => {
        refresh();
    });
    return { content, options, refresh };
}
